/* =============================================
reset
============================================= */
p {
  font-size: 14px;
}

h1 {
  font-size: 36px;
  font-weight: 700;
}

h2 {
  font-size: 32px;
  font-weight: 700;
}

h3 {
  font-size: 24px;
  font-weight: 700;
}

h4 {
  font-size: 20px;
  font-weight: 500;
}

h5 {
  font-size: 14px;
  font-weight: 400;
}

h6 {
  font-size: 13px;
  font-weight: 400;
}

p, h1, h2, h3, h4, h5, h6 {
  color: #222;
  margin: 0;
}

.fw100 {font-weight: 100;}
.fw200 {font-weight: 200;}
.fw300 {font-weight: 300;}
.fw400 {font-weight: 400;}
.fw500 {font-weight: 500;}
.fw600 {font-weight: 600;}
.fw700 {font-weight: 700;}
.fw800 {font-weight: 800;}
.fw900 {font-weight: 900;}

.mt5 {margin-top: 5px;}
.mt10 {margin-top: 10px;}
.mt15 {margin-top: 15px;}
.mt20 {margin-top: 20px;}
.mt25 {margin-top: 25px;}
.mt30 {margin-top: 30px;}
.mt35 {margin-top: 35px;}
.mt40 {margin-top: 40px;}
.mt45 {margin-top: 45px;}
.mt50 {margin-top: 50px;}
.mt55 {margin-top: 55px;}
.mt60 {margin-top: 60px;}

a, a:link, a:visited, a:active, a:hover {
  text-decoration: none;
  color: #222;
  cursor: pointer;
}

a, input, button {
  -webkit-tap-highlight-color : transparent;
}

ul {
  list-style: none;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0;
}

button {
  background-color: #fff;
  border: 1px solid #222;
  cursor: pointer;
}

button:hover {
  background-color: none;
}

input:focus {
  outline: none;
}

div, header, input {
  box-sizing: border-box;
}

@keyframes fadein {
  0% {opacity: 0;};
  100% {opacity: 100%;}
}

/* =============================================
App
============================================= */

.Home, .Shop, .Community, .MyPage, .Tips {
  margin: 80px 0;
  padding: 0 20px;
  background-color: #fff;
  animation: fadein .1s;
}

/* =============================================
Header
============================================= */

.App-header {
  width: 100%;
  height: 60px;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  position: fixed;
  top: 0;
  z-index: 9999;
}

.logo div {
  width: 60px;
  height: 40px;
  background: url(../src/assets/logo.svg) center no-repeat;
  background-size: 60px;
}

.nav {
  display: flex;
}

.nav .tips {
  font-size: 14px;
  color: #ff5f6d;
  border: 1px solid #ff5f6d;
  border-radius: 50px;
  padding: 5px 10px;
}


.nav a + a {
  margin-left: 20px;
}

.slide-menu {
  width: 200px;
  height: 200vh;
  background-color: blue;
  position: absolute;
  right: -200px;
  transition: .3s;
}

/* =============================================
home
============================================= */

.nav-con-bottom {
  width: 100%;
  height: 60px;
  background-color: #fff;
  border-top: 1px solid #eee;
  border-radius: 30px 30px 0 0;
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
}

.nav-con-bottom a {
  width: 25%;
  height: 60px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

.nav-con-bottom a .btn-tab {
  display: none;
  width: 20px;
  height: 20px;
  color: black;
}

.nav-con-bottom .link-tab .btn-tab.on, 
.nav-con-bottom .link-tab.on .btn-tab {
  display: none;
}
.nav-con-bottom .link-tab .btn-tab,
.nav-con-bottom .link-tab.on .btn-tab.on {
  display: block;
}

.nav-con-bottom a p {
  font-size: 11px;
  color: #777;
  margin-top: 6px;
  text-align: center;
  position: relative;
}

.nav-con-bottom a.on p {
  color: #ff5f6d;
  font-weight: 700;
}

.main-con ul .wel-box h3 {
  font-weight: 400;
  line-height: 1.4;
}

.main-con ul .wel-box .user-name span {
  font-weight: 900;
}

.main-con ul li + li {
  margin-top: 50px;
}

.main-con ul li .tit {
  font-weight: 700;
}

.main-con ul li .sub-tit {
  color: #999;
  margin-bottom: 4px;
}

.main-con ul li .banner {
  width: 100%;
  aspect-ratio: 2.5 / 1;
  background-color: #eee;
  margin-top: 10px;
  border-radius: 10px;
}

.main-con ul li .btn-report {
  width: 100%;
  height: 40px;
  color: #ff5f6d;
  border: 1px solid #ff5f6d;
  border-radius: 6px;
  margin-top: 10px;
}

.main-con ul .main-box01 .banner {
  width: 100%;
  height: 100%;
  background: url(../src/assets/img-banner_01.jpg) center no-repeat;
  background-size: 100%;
  background-position: top;
}
.main-con ul .main-box02 .banner {
  width: 100%;
  height: 100%;
  background: url(../src/assets/img-banner_02.jpg) center no-repeat;
  background-size: 100%;
  background-position: center;
}

.main-con ul .swiper-box01 .mySwiper {
  margin-left: -20px;
  margin-right: -20px;
  padding-left: 20px;
  padding-right: 20px;
}

.main-con ul .swiper-box01 .mySwiper .slide {
  aspect-ratio: 1.2 / 1;
  background-color: #eee;
  border-radius: 10px;
  margin-top: 10px;
  width: 100%;
  height: auto;
  background-size: cover;
}

.main-con ul .swiper-box01 .mySwiper .slide.slide01 {
  background: url(../src/assets/img-swiper_01.jpg) center no-repeat;
  background-size: cover;
}

.main-con ul .swiper-box01 .mySwiper .slide.slide02 {
  background: url(../src/assets/img-swiper_02.jpg) center no-repeat;
  background-size: cover;
}

.main-con ul .swiper-box01 .mySwiper .slide.slide03 {
  background: url(../src/assets/img-swiper_03.jpg) center no-repeat;
  background-size: cover;
}

.main-con ul .swiper-box01 .mySwiper .slide.slide04 {
  background: url(../src/assets/img-swiper_04.jpg) center no-repeat;
  background-size: cover;
}

.main-con ul .swiper-box01 .mySwiper .slide.slide05 {
  background: url(../src/assets/img-swiper_05.jpg) center no-repeat;
  background-size: cover;
}

.main-con ul .swiper-box01 .mySwiper .slide.slide06 {
  background: url(../src/assets/img-swiper_06.jpg) center no-repeat;
  background-size: cover;
}

/* =============================================
login
============================================= */

.Login {
  width: 100%;
  height: 50vh;
  background-color: #fff;
  position: fixed;
  bottom: 0;
  z-index: 9999;
  animation: fadein .5s;
}

.dimmed {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 50%;
  z-index: -1;
}

.login-con {
  background-color: #fff;
  width: 100%;
  height: 50vh;
  border-radius: 20px 20px 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  position: relative;
}

.login-con .btn-close {
  position: absolute;
  right: 20px;
  top: 20px;
  width: 20px;
  height: 20px;
  background: url(../src/assets/btn-close.svg) center no-repeat;
  background-size: 20px;
}

.login-box, .sns-login {
  width: 100%;
  display: flex;
  flex-flow: column;
  align-items: center;
}

.login-box {
  justify-content: space-between;
  height: 50vh;
  padding: 80px 20px;
}

.login-box > div:last-of-type {
  margin-top: 40px;
}

.login-box .direct-login h3 {
  text-align: center;
}

.login-box .sns-login button {
  width: 100%;
  height: 50px;
  border-radius: 6px;
  border: none;
  font-size: 16px;
  font-weight: 700;
}

.login-box .sns-login button + button {
  margin-top: 8px;
}

.login-box .sns-login .btn-kakao {
  border: 1px solid #eee;
  color: #222;
}

.login-box .sns-login .btn-naver {
  border: 1px solid#eee;
  color: #222;
}

/* =============================================
shop
============================================= */

.Shop {
  width: 100%;
  height: calc(100vh - 160px);
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
}

.Shop button {
  padding: 0 30px;
  height: 40px;
  background-color: #ff5f6d;
  color: #fff;
  border-radius: 6px;
  border: none;
  font-size: 16px;
  font-weight: 700;
  margin-top: 20px;
}